<template>
	<view class="page">
		<view class="share-container">

			<text class="share-label">标题</text>
			<u-input class="share-input" v-model="title" type="textarea" :border="true" border-color="#333333"
				:clearable="true" placeholder="请输入标题" :trim="true" />
			<text class="share-label">链接</text>
			<u-input class="share-input" v-model="link" type="textarea" :border="true" border-color="#333333"
				:clearable="true" placeholder="请输入链接" :trim="true" />

			<view class="share-submit" @click="submit">提交</view>

		</view>
	</view>
</template>

<script>
	import api from '@/http/ApiService.js'
	import checker from '@/utils/Checker.js'
	var self
	export default {
		onLoad() {
			self = this
		},
		data() {
			return {
				title: '',
				link: ''
			}
		},
		methods: {
			submit() {
				console.log(self.title, self.link)
				if (checker.checkShareAdd(self.title, self.link)) {
					api.myShareAdd(self.title, self.link).then(res => {
						getApp().globalData.refresh.shareRefresh = true
						uni.navigateBack({})
					})
				}
			}
		}
	}
</script>

